/* src/App.css */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1a1a1a;
  color: #fff; /* 设置全局字体颜色为白色 */
}

.App {
  position: relative; /* 父容器需要相对定位，以便子绝定定位 */
  text-align: center;
}

/* --- 排行榜样式 --- */
.leaderboard-container {
  position: absolute; /* 绝对定位，浮动在地图上 */
  bottom: 20px;
  left: 20px;
  width: 320px;
  background-color: rgba(26, 26, 26, 0.8); /* 半透明背景 */
  border: 1px solid #00aaff;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 0 15px rgba(0, 170, 255, 0.5);
  text-align: left;
  z-index: 10; /* 确保在ECharts图表之上 */
}

.leaderboard-title {
  margin: 0 0 10px 0;
  font-size: 16px;
  color: #00aaff;
  text-align: center;
  border-bottom: 1px solid rgba(0, 170, 255, 0.3);
  padding-bottom: 10px;
}

.leaderboard-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.leaderboard-item {
  display: flex;
  align-items: center;
  padding: 8px 5px;
  font-size: 14px;
  border-bottom: 1px solid #323c48;
}

.leaderboard-item:last-child {
  border-bottom: none;
}

.leaderboard-item .rank {
  font-weight: bold;
  width: 50px;
}

.leaderboard-item .rank-1 { color: #ff4d4f; }
.leaderboard-item .rank-2 { color: #ff7a45; }
.leaderboard-item .rank-3 { color: #ffa940; }

.leaderboard-item .country-info {
  flex-grow: 1; /* 占据剩余空间 */
}

.leaderboard-item .speed-info {
  font-family: 'Courier New', Courier, monospace;
  font-size: 13px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.speed-info .speed-up {
  color: #a0d911; /* 绿色代表上行 */
}

.speed-info .speed-down {
  color: #40a9ff; /* 蓝色代表下行 */
}
